import uniSegmentedControl from "@/components/uni-segmented-control/uni-segmented-control.vue"
export default {
	components: {uniSegmentedControl},
	data() {
		return {
			items: ['全部','办理中','已完成','已退单'],
			states: [
				'未完成',
				'已完成',
			],
			current: 0,
			isLogin: getApp().globalData.userId,
			list: null
		}
	},
	created (){
		this.getList('all');
	},
	methods: {
		getList (index) {
			this.list = null
			const params = {
				user_id: getApp().globalData.userId,
				status: index < 0 ? 'all' : index
			}
			this.$api.orderList(params).then(res => {
				this.list = res.data
			})
		},
		goDetail(id) {
			uni.navigateTo({
				url: `/pages/orderInfo/index?id=${id}`
			});
		},
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
			this.getList((index - 1))
		},
		getAuth () {
			my.getAuthCode({
				scopes: ['auth_user'],
				success: (res) => {
					console.log(res, 111)
					this.auth(res.authCode)
				},
			});
		},
		auth (auth){
			const params = {
				'auth_code': auth
			}
			this.$api.auth(params).then(res => {
				getApp().globalData.userId = res.user_id
				this.getList(-1)
			}).catch(err => {
				console.log(err)
			})
		},
	}
}
